import React from 'react'
import Layout from "../defaults/layout"
import SEO from "../defaults/seo"
import "../../style/studio/studio.scss"
import { Link } from "gatsby"

const back = require("../../images/studio/backArrow.png")

const StudioLayout = ({ children }) =>
{
    const [year, activeYear] = React.useState(null)

    const locationChange = () =>
    {
        let pathname = window.location.pathname;
        if (pathname === "/studio/")
        {
            activeYear(null)
        }
        else
        {
            activeYear(pathname.substr(-4))
        }
    }

    React.useEffect(() =>
    {
        locationChange();
    }, [])

    return (<Layout>
        <SEO title="Page two" />
        <div className="studio-card-content">

            <div className="flex-between studio-head container">
                <h1>Navasardi studio</h1>
                <div className="butterfly" />
                <div>
                    <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.</p>
                    {!!year && <div className="backLink">
                        <div><Link to="/studio/"><img src={back} alt="" />{" Back"}</Link> </div>

                        <span>{year}</span>
                    </div>}
                </div>

            </div>
            {children}
        </div>

    </Layout>)
}

export default StudioLayout